import { LitElement, html } from 'lit';
import { customElement } from 'lit/decorators.js';

import { router } from '../app-router';

@customElement('app-home')
export class AppMenu extends LitElement {
  createRenderRoot() {
    return this;
  }

  render() {
    return html`
    <div id="app-index__menu-container">
      <ul class="app-index__menu">
        <li><a href="${router.urlForPath('game')}" data-route="game" tabindex="0" role="button">START</a></li>
        <li><a tabindex="0" href="${router.urlForPath('help')}" data-route="help" role="button">HELP</a></li>
        <!-- li><a tabindex="0" role="button">HELP</a></li>
        <li><a tabindex="0" role="button">CREDITS</a></li -->
      </ul>
    </div>
    `;
  }
}